import Vue from 'vue'
import Vuex from 'vuex'
import { setItem, getItem } from '@/utils/storage'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    token: getItem('token')

  },
  mutations: {
    addtoken (state, data) {
      state.token = data
      setItem('token', state.token)
    },
    gettoken (state) {
      return state.token
    }
  },
  actions: {
  },
  modules: {
  }
})
