import Vue from 'vue'
import VueRouter from 'vue-router'
// import cs from '../views/cs.vue'
import home from '@/views/home'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/home'

  },
  {
    path: '/home',
    component: home,
    children: [
      {
        path: '',
        component: () => import('@/views/home/shouye')
      },
      {
        path: 'wenda',
        component: () => import ('@/views/home/wenda')
      },
      {
        path: 'shipin',
        component: () => import ('@/views/home/shipin')
      },
      {
        path: 'wode',
        component: () => import ('@/views/home/wode')
      },
      {
        path: 'userinfo',
        component: () => import ('@/views/home/userinfo')
      },
      {
        // ?代表type可有可无
        path: 'myartical/:type?',
        component: () => import('@/views/home/components/myartical'),
        props: true
      },
      {
        path: 'xiaozhi',
        component: () => import ('@/views/home/components/xiaozhi')
      }
    ]
  }, {
    path: '/login',
    name: 'login',
    component: () => import ('@/views/login')
  }, {
    path: '/search',
    component: () => import('@/views/search'),
    name: 'search'
  },
  {
    path: '/articale/:art_id',
    component: () => import('@/views/artical'),
    name: 'artical',
    props: true
  }
  // {
  //   path: '/',
  //   name: 'home',
  //   component: Home
  // },
  // {
  //   path: '/about',
  //   name: 'about',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // }
]

const router = new VueRouter({
  routes
})

export default router
